import axios from "axios";
let APIURL;
if (process.env.NODE_ENV === "development") {
  APIURL = "http://localhost:4000";
} else {
  APIURL = "https://alkemy-back-challenge.herokuapp.com";
}
const baseURL = `${APIURL}/api/users`;

export const getUser = async (credentials, tokenId) => {
  try {
    if (!tokenId) {
      const { data } = await axios.post(`${baseURL}/login`, credentials);
      console.log(data);
      return data;
    } else {
      const { data } = await axios.post(`${baseURL}/googleAuth`, {tokenId});
      console.log(data);
      return data;
    }
  } catch (error) {
    console.log(error);
  }
};
