import { useState, useContext } from "react";
import { useHistory } from "react-router";
import { registerUser } from "../../services/register";
import FormUserRegister from "../../components/Forms/FormUserRegister";
import ModalWindow from "../../components/Modals/ModalWindow";
import CenterContainer from "../../components/Containers/CenterContainer";
import { AuthContext } from "../../AuthContext";

const Register = () => {
  const history = useHistory();

  const [open, setOpen] = useState(false);

  const [error, setError] = useState("");

  const { signin } = useContext(AuthContext);

  const handleRegisterUser = async (values) => {
    const { tokenId } = values
    try {
      const { data } = await registerUser(values, tokenId);
      if (!data.errors) {
        if (tokenId){
          signin(data)
          return history.push('/')
        }
        setOpen(true);
      } else {
        setError(data.errors);
      }
    } catch (error) {
      setError("This username has been used");
    }
  };


  const handleConfirm = () => {
    setOpen(false);
    history.push("/login");
  };
  return (
    <CenterContainer>
      <FormUserRegister
        onSubmit={handleRegisterUser}
        error={error}
      />
      <ModalWindow
        modalText={"Your account has been created successfully!"}
        open={open}
        handleConfirm={handleConfirm}
        type="confirm"
      />
    </CenterContainer>
  );
};

export default Register;
