import { PlusCircleIcon } from '@heroicons/react/solid';

const BalanceBox = ({...props}) => {
     const {total, handleOpenModal} = props
    return (
        <div className=' flex flex-col text-indigo-900 mt-10'>
        <h1 className='mx-auto text-2xl sm:text-3xl text-center font-semibold'>
            Your balance is
            <strong className={`${total >= 0 ? "text-green-500" : "text-red-500"}`}>
            {" "}{total<0 && "-"}{"$"}{Math.abs(total)}
            </strong>
        </h1>
        <div className='text-center font-semibold  inline-flex w-full justify-center items-center my-2  '>
            <button className=' px-4 py-1 bg-indigo-600 text-white rounded-md shadow-md inline-flex items-center hover:bg-indigo-800 transform hover:scale-105 duration-300 ease-in-out ' onClick={handleOpenModal} >
            New
            <PlusCircleIcon className=' cursor-pointer h-8 ml-1 ' />
            </button>
        </div>
    </div> 
    )
}
export default BalanceBox